body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 5.4285em;
    width: 100%;
    height: 100%;
    background-image: url(/personalwebsite/static/media/background.a9532bb2.jpg);
    cursor: pointer;
    overflow: hidden;
}

#root {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

.containerDiv > div{
    cursor: pointer;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 800;
    will-change: transform, opacity;
    /* text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerFont {
  font-family: 'Bungee Shade', cursive !important;
  font-size: 50px !important;
}

.amaticFont {
  font-family: 'Amatic SC', cursive;
  font-size: xx-large;
  opacity: 0.5;
}

.alignLeft {
  text-align: left;
}

.alignRight {
  text-align: right;
}

.shortTagLineFont {
    font-family: 'Unica One', cursive !important;
    font-size: 35px !important;
}

.appDataFont, div> span {
    font-family: 'Alegreya Sans SC', sans-serif !important;
    font-size: 35px !important;
}

li {
    text-align: center !important;
}

h1 {
    color: #2a8486 !important;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

